import "./about.css";
import React from "react";
import Me from "../../assets/portf.jpg";
import { FaAward } from "react-icons/fa";
import { FiUsers } from "react-icons/fi";
import { TiFolderOpen } from "react-icons/ti";

export default function About() {
  return (
    <section id="about">
      <h5>Get To Know </h5>
      <h2>About Me</h2>
      <div className="container about_container">
        <div className="about_me">
          <div className="about_me-image">
            <img src={Me} alt="About image" />
          </div>
        </div>
        <div className="about_content">
          <div className="about_cards">
            <article className="about_card">
              <FaAward className="about_icon" />
              <h5>Experiences</h5>
              <small>Not yet</small>
            </article>
            <article className="about_card">
              <FiUsers className="about_icon" />
              <h5>Client</h5>
              <small>300+ wolrdwide</small>
            </article>
            <article className="about_card">
              <TiFolderOpen className="about_icon" />
              <h5>Project</h5>
              <small>3 Completed</small>
            </article>
          </div>
          <p>
            An “About Me,” also known as a blurb, is a short piece of writing
            that informs your reader about your professional background, key
            accomplishments, personal values and any brands you may be
            associated with. Personal websites, social media profiles and
            company directories often feature “About Me” sections
          </p>
          <a href="#contact" className="btn btn-primary">
            Let's Talk
          </a>
        </div>
      </div>
    </section>
  );
}
