import "./contact.css";
import { AiOutlineMail } from "react-icons/ai";
import { AiOutlineLinkedin } from "react-icons/ai";
import { ImWhatsapp } from "react-icons/im";
import React, { useRef } from "react";
import emailjs from "emailjs-com";

export default function Contact() {
  const form = useRef();
  const sendEmail = (e) => {
    e.preventDefault();

    emailjs.sendForm(
      "service_eyblhxn",
      "template_16duqxh",
      form.current,
      "vBxZDL3YZn-Vpa-dp"
    );
    e.target.reset();
  };
  return (
    <section id="contact">
      <h5>Get In Touch</h5>
      <h2>Contact me</h2>
      <div className="container contact_container">
        <div className="contact_options">
          <article className="contact_option">
            <AiOutlineMail className="contact_option-icon" />
            <h4>Email</h4>
            <h5>praveenme117@gmail.com</h5>
            <a href="mailto:praveenme117@gmail.com" target="_blank">
              Send a message
            </a>
          </article>
          <article className="contact_option">
            <AiOutlineLinkedin className="contact_option-icon" />
            <h4>LinkedIn</h4>
            <a
              href="https://www.linkedin.com/in/praveen-kumar-974b11204/"
              target="_blank"
            >
              Send a message
            </a>
          </article>
          <article className="contact_option">
            <ImWhatsapp className="contact_option-icon" />
            <h4>WhatsApp</h4>
            <h5>+917491059562</h5>
            <a
              // href="https://api.whatsapp.com/send/?phone=%2B918757749470"
              target="_blank"
            >
              Send a message
            </a>
          </article>
        </div>
        {/* End of contact option */}
        <form ref={form} onSubmit={sendEmail}>
          <input
            type="text"
            name="name"
            placeholder="Your Full Name"
            required
          />
          <input type="mail" name="email" placeholder="Your Mail" required />
          <textarea
            name="message"
            rows="7"
            placeholder="Your message"
          ></textarea>
          <button type="submit" className="btn btn-primary">
            Send Message
          </button>
        </form>
      </div>
    </section>
  );
}
